export default defineNuxtPlugin((nuxtApp) => {
    nuxtApp.vueApp.config.errorHandler = (error: Error, instance) => {
        // handle error, e.g. report to a service
        if (error.name === 'NoActiveSocket') {
            setTimeout(() => {
                instance.$forceUpdate()
            }, 5000)
        }
        if (error.message === 'There was a problem with the database: The token has expired') {

            setTimeout(() => useRouter().push({path: '/signin'}), 500)
        }
    }

    // Also possible
    nuxtApp.hook('vue:error', (error: Error, instance, info) => {
        // handle error, e.g. report to a service
        console.log('vue:err', error.message,error.name, instance, info);
    })
})
