/** */
const enduring: { [key: string]: () => Ref<any> } = {
    useCurrentUser
}


//app.vueonMounted
/**
 * @param key 
 */
export const setLocal = (key?: string) => {
    if (key) {
        console.log('', key);
        const useKey = 'use' + key.slice(0, 1).toUpperCase() + key.slice(1).toLowerCase();//
        const func = enduring[useKey]
        if (!func) {
            console.log('', useKey, '');
            return
        }
        const va = func().value;
        localStorage.setItem(key, JSON.stringify(va))
    } else {
        console.log('');
        for (const key in enduring) {
            if (Object.prototype.hasOwnProperty.call(enduring, key)) {
                const element = enduring[key];
                const setKey = key.toLowerCase().substring(3)//use 
                try {
                    const va = element().value;
                    localStorage.setItem(setKey, JSON.stringify(va));
                } catch (error) {
                    console.log(`${setKey}`, error);
                }
            }
        }
    }
}
/**state */
export const getLoacl = () => {
    for (const key in enduring) {
        if (Object.prototype.hasOwnProperty.call(enduring, key)) {
            const element = enduring[key];
            const setKey = key.toLowerCase().substring(3)//use 
            try {
                const localData = localStorage.getItem(setKey) || '';
                if (localData && localData != 'undefined') {
                    element().value = JSON.parse(localData);
                    console.log(setKey, '', element().value);
                }
            } catch (error) {
                console.log(`${setKey}`, error);
            }
        }
    }
}
